export default {
    data() {
        return {
            password: '',
            password_confirmation: '',
            loadingState: false,
        };
    },
    methods: {
        async resetPassword() {
            const token = this.$route.query.token;
            try {
                await this.$axios.post(
                    process.env.BASE_URL + '/reset-password/' + token,
                    {
                        password: this.password,
                        password_confirmation: this.password_confirmation
                    }
                );
            } catch (error) {
                console.log(error);
            }
        }
    }
};
